import React ,{useState}from "react";
import { useSelector } from "react-redux";
import { updateUserData } from "../../../store/actions/User";
import "./Profile.css";
import { useDispatch } from "react-redux";
import { useRouteMatch,Switch, Route } from "react-router-dom";
 
import Input from "../../UI/Input";

import Loading from "../../Loading/Loading";
import EditCard from "../../UI/EditCard";
import Address from "./Address";
const AddressHome =({baseUrl}) =>{

  // const {category} = useParams();
  const {url,path} =useRouteMatch();
  const dispatch = useDispatch();

  const userdata = useSelector((state)=>state?.user?.user);
  //console.log(userdata)
  const [data, setData]= useState({field:'',title:'',value:'',id:userdata?.user?.id?userdata?.user?.id:'',token:userdata?.token?userdata?.token:''});
   const [showSnackBar , setSnackBar] = useState({status:false,show:false})
  
 const [loader , setLoader] =useState({status:false});
/* handle Login function  */
const handleUpdate=(e)=>{
    e.preventDefault();
  
setLoader({status:true});
     const user = dispatch(updateUserData(data));

    user.then(response => {
//    console.log(response?.user?.firstName);

if(response?.user){
  //  console.log(response?.user?.lastName);
  setSnackBar({status:true,show:true});
 
  setTimeout(function(){setSnackBar({status:false}); }, 3000);
}
else{
    setSnackBar({status:false,show:true});
 

};
  
setLoader({status:false});



 }).catch(error => {
   //console.error(error);
   setSnackBar({status:false,show:true});
   setLoader({status:false});

 });

    

}
const inputGetValue = 
    (e)=>
    {
             setData({...data,field:e.target.name,title:e.target.title, value:e.target.value});
        
   
    }
    
    return(
    <> 
{<Loading Load={loader?.status}/>}
{ (showSnackBar.status && showSnackBar.show ) ?  
<div id="snackbarUser" >{data.value?data.title +"has been Changed to "+data.value : data.title +" has been Removed "}.</div> : ''  }
 { (!showSnackBar.status && showSnackBar.show ) ? 
  <div id="snackbarUser" >Couldn`t update {data.title}  | Error Occured.</div> : '' }


<h3> Home Address </h3>
    

<EditCard  
 url={url} name="homeCity"  icon="" title="Home City" button=""
value=
{userdata?.user?.address?.homeAddress?.homeCity?userdata?.user?.address?.homeAddress?.homeCity:''}
/>

<EditCard  
 url={url} name="homeState"  icon="" title="Home State" button=""
value=
{userdata?.user?.address?.homeAddress?.state?userdata?.user?.address?.homeAddress?.state:''}
/>

<EditCard  
 url={url} name="homeCountry"  icon="" title="Home Country" button=""
value=
{userdata?.user?.address?.homeAddress?.country?userdata?.user?.address?.homeAddress?.country:''}
/>
<EditCard  
 url={url} name="homeZip"  icon="" title="Home Zip/Pin" button=""
value=
{userdata?.user?.address?.homeAddress?.zipOrPin?userdata?.user?.address?.homeAddress?.zipOrPin:''}
/>
<Address baseUrl={baseUrl}/>
<Switch>

<Route path={`${path}/homeCity`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="homeCity" type="text" title=" Home City" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.homeAddress?.homeCity?userdata?.user?.address?.homeAddress?.homeCity:''}
 placeholder="Add Home City" />
 
 </form>
    </Route>
    
<Route path={`${path}/homeState`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="homeState" type="text" title=" Home State" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.homeAddress?.state?userdata?.user?.address?.homeAddress?.state:''}
 placeholder="Add Home State" />
 
 </form>
    </Route>
<Route path={`${path}/homeCountry`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="homeCountry" type="text" title=" Home Country" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.homeAddress?.country?userdata?.user?.address?.homeAddress?.country:''}
 placeholder="Add Home Country" />
 
 </form>
    </Route>
<Route path={`${path}/homeZip`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="homeZip" type="number" title=" Home Zip/Pin" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.homeAddress?.country?userdata?.user?.address?.homeAddress?.country:''}
 placeholder="Add Home Zip/Pin" />
 
 </form>
    </Route>
</Switch>

    </>)
}
export default AddressHome;