import React from "react";
import { Link } from "react-router-dom";

import createIcon from "../../icons/add_circle_FILL0_wght200_GRAD0_opsz40.svg";

import Card from "../../UI/Card";
const Address =({baseUrl}) =>{

    
    return(<>  
    <h3> Add Address </h3>
    <div className="gridCardProfile">
 
    <Link to={`${baseUrl}/current-address`} >
   <Card  icon={createIcon} alt="create" title="Current Address"  >

    </Card>
   </Link> 
   <Link to={`${baseUrl}/home-address`} >
   <Card  icon={createIcon} alt="create" title="Home Address"  >

    </Card>
   </Link> 
</div>
    </>)
}
export default Address;