import React ,{useState}from "react";
import { useSelector } from "react-redux";
import { updateUserData } from "../../../store/actions/User";
import "./Profile.css";
import { useDispatch } from "react-redux";
import { useRouteMatch,Switch, Route } from "react-router-dom";
 
import Input from "../../UI/Input";

import Loading from "../../Loading/Loading";
import EditCard from "../../UI/EditCard";
const Education =() =>{

  // const {category} = useParams();
  const {url,path} =useRouteMatch();
  const dispatch = useDispatch();

  const userdata = useSelector((state)=>state?.user?.user);
  //console.log(userdata)
  const [data, setData]= useState({field:'',title:'',value:'',id:userdata?.user?.id?userdata?.user?.id:'',token:userdata?.token?userdata?.token:''});
   const [showSnackBar , setSnackBar] = useState({status:false,show:false})
  
 const [loader , setLoader] =useState({status:false});
/* handle Login function  */
const handleUpdate=(e)=>{
    e.preventDefault();
  
setLoader({status:true});
     const user = dispatch(updateUserData(data));

    user.then(response => {
//    console.log(response?.user?.firstName);

if(response?.user){
  //  console.log(response?.user?.lastName);
  setSnackBar({status:true,show:true});
 
  setTimeout(function(){setSnackBar({status:false}); }, 3000);
}
else{
    setSnackBar({status:false,show:true});
 

};
  
setLoader({status:false});



 }).catch(error => {
   //console.error(error);
   setSnackBar({status:false,show:true});
   setLoader({status:false});

 });

    

}
const inputGetValue = 
    (e)=>
    {
             setData({...data,field:e.target.name,title:e.target.title, value:e.target.value});
        
   
    }
    
    return(
    <> 
{<Loading Load={loader?.status}/>}
{ (showSnackBar.status && showSnackBar.show ) ?  
<div id="snackbarUser" >{data.value?data.title +"has been Changed to "+data.value : data.title +" has been Removed "}.</div> : ''  }
 { (!showSnackBar.status && showSnackBar.show ) ? 
  <div id="snackbarUser" >Couldn`t update {data.title}  | Error Occured.</div> : '' }


<h3> Education</h3>
    

<EditCard  
 url={url} name="highestEducation"  icon="" title="Education" button=""
value=
{userdata?.user?.education?.highestEducation?userdata?.user?.education?.highestEducation:''}
/>


<EditCard  
 url={url} name="collegeName"  icon="" title="College Name" button=""
value=
{userdata?.user?.education?.collegeName?userdata?.user?.education?.collegeName:''}
/>


<EditCard  
 url={url} name="percentageOrCGPA"  icon="" title="% Or CGPA" button=""
value=
{userdata?.user?.education?.percentageOrCGPA?userdata?.user?.education?.percentageOrCGPA:''}
/>


<EditCard  
 url={url} name="tenthPercentage"  icon="" title="Tenth %" button=""
value=
{userdata?.user?.education?.tenthPercentage?userdata?.user?.education?.tenthPercentage:''}
/>


<EditCard  
 url={url} name="twelthPercentage"  icon="" title="Twelth %" button=""
value=
{userdata?.user?.education?.twelthPercentage?userdata?.user?.education?.twelthPercentage:''}
/>


<EditCard  
 url={url} name="UGPercentageOrCGPA"  icon="" title="UG % Or CGPA" button=""
value=
{userdata?.user?.education?.myUGPercentageOrCGPA?userdata?.user?.education?.myUGPercentageOrCGPA:''}
/>


<EditCard  
 url={url} name="PGPercentageOrCGPA"  icon="" title="PG % Or CGPA" button=""
value=
{userdata?.user?.education?.myPGPercentageOrCGPA?userdata?.user?.education?.myPGPercentageOrCGPA:''}
/>


<Switch>

<Route path={`${path}/highestEducation`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="highestEducation" type="text" title=" Highest Education " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.highestEducation?userdata?.user?.education?.highestEducation:''}
 placeholder="Add Highest Education" />
 
 </form>
    </Route>


    <Route path={`${path}/collegeName`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="collegeName" type="text" title=" College Name " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.collegeName?userdata?.user?.education?.collegeName:''}
 placeholder="Add College Name" />
 
 </form>
    </Route>
<Route path={`${path}/percentageOrCGPA`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="percentageOrCGPA" type="number" title="Highest Percentage Or CGPA " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.percentageOrCGPA?userdata?.user?.education?.percentageOrCGPA:''}
 placeholder="Add  Percentage Or CGPA" />
 
 </form>
    </Route>
<Route path={`${path}/tenthPercentage`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="tenthPercentage" type="number" title=" Tenth Percentage " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.tenthPercentage?userdata?.user?.education?.tenthPercentage:''}
 placeholder="Add Tenth Percentage" />
 
 </form>
    </Route>
<Route path={`${path}/twelthPercentage`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="twelthPercentage" type="number" title=" Twelth Percentage" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.twelthPercentage?userdata?.user?.education?.twelthPercentage:''}
 placeholder="Add Twelth Percentage" />
 
 </form>
    </Route>
<Route path={`${path}/UGPercentageOrCGPA`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="myUGPercentageOrCGPA" type="number" title="  UG Percentage Or CGPA " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.myUGPercentageOrCGPA?userdata?.user?.education?.myUGPercentageOrCGPA:''}
 placeholder="Add UG Percentage Or CGPA" />
 
 </form>
    </Route>
    <Route path={`${path}/PGPercentageOrCGPA`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="myPGPercentageOrCGPA" type="number" title="  PG Percentage Or CGPA " baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.education?.myPGPercentageOrCGPA?userdata?.user?.education?.myPGPercentageOrCGPA:''}
 placeholder="Add PG Percentage Or CGPA" />
 
 </form>
    </Route>

</Switch>

    </>)
}
export default Education;