import React ,{useState}from "react";
import { useSelector } from "react-redux";
import { updateUserData } from "../../../store/actions/User";
import "./Profile.css";
import { useDispatch } from "react-redux";
import { useRouteMatch,Switch, Route } from "react-router-dom";
 
import Input from "../../UI/Input";

import Loading from "../../Loading/Loading";
import EditCard from "../../UI/EditCard";
const CareerDevelopment =() =>{

  // const {category} = useParams();
  const {url,path} =useRouteMatch();
  const dispatch = useDispatch();

  const userdata = useSelector((state)=>state?.user?.user);
  //console.log(userdata)
  const [data, setData]= useState({field:'',title:'',value:'',id:userdata?.user?.id?userdata?.user?.id:'',token:userdata?.token?userdata?.token:''});
   const [showSnackBar , setSnackBar] = useState({status:false,show:false})
  
 const [loader , setLoader] =useState({status:false});
/* handle Login function  */
const handleUpdate=(e)=>{
    e.preventDefault();
  
setLoader({status:true});
     const user = dispatch(updateUserData(data));

    user.then(response => {
//    console.log(response?.user?.firstName);

if(response?.user){
  //  console.log(response?.user?.lastName);
  setSnackBar({status:true,show:true});
 
  setTimeout(function(){setSnackBar({status:false}); }, 3000);
}
else{
    setSnackBar({status:false,show:true});
 

};
  
setLoader({status:false});



 }).catch(error => {
   //console.error(error);
   setSnackBar({status:false,show:true});
   setLoader({status:false});

 });

    

}
const inputGetValue = 
    (e)=>
    {
             setData({...data,field:e.target.name,title:e.target.title, value:e.target.value});
        
   
    }
    
    return(
    <> 
{<Loading Load={loader?.status}/>}
{ (showSnackBar.status && showSnackBar.show ) ?  
<div id="snackbarUser" >{data.value?data.title +"has been Changed to "+data.value : data.title +" has been Removed "}.</div> : ''  }
 { (!showSnackBar.status && showSnackBar.show ) ? 
  <div id="snackbarUser" >Couldn`t update {data.title}  | Error Occured.</div> : '' }


<h3> Career Development</h3>
    

<EditCard  
 url={url} name="resume"  icon="" title="Resume " button=""
value=
{userdata?.user?.careerDevelopment?.resume?userdata?.user?.careerDevelopment?.resume:''}
/>

<EditCard  
 url={url} name="cdp"  icon="" title="CDP" button=""
value=
{userdata?.user?.careerDevelopment?.cdp?userdata?.user?.careerDevelopment?.cdp:''}
/>

<EditCard  
 url={url} name="swot"  icon="" title="SWOT " button=""
value=
{userdata?.user?.careerDevelopment?.swot?userdata?.user?.careerDevelopment?.swot:''}
/>

<EditCard  
 url={url} name="journey"  icon="" title="Journey" button=""
value=
{userdata?.user?.careerDevelopment?.journey?userdata?.user?.careerDevelopment?.journey:''}
/>

<EditCard  
 url={url} name="technical"  icon="" title="Technical" button=""
value=
{userdata?.user?.careerDevelopment?.technical?userdata?.user?.careerDevelopment?.technical:''}
/>

<EditCard  
 url={url} name="videoResume"  icon="" title="Video Resume" button=""
value=
{userdata?.user?.careerDevelopment?.videoResume?userdata?.user?.careerDevelopment?.videoResume:''}
/>

<Switch>

<Route path={`${path}/resume`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="resume" type="text" title=" Resume Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.resume?userdata?.user?.careerDevelopment?.resume:''}
 placeholder="Add Resume Link" />
 
 </form>
    </Route>
<Route path={`${path}/cdp`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="cdp" type="text" title=" Career Development Plan Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.cdp?userdata?.user?.careerDevelopment?.cdp:''}
 placeholder="Add Career Development Plan Link" />
 
 </form>
    </Route>
<Route path={`${path}/swot`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="swot" type="text" title=" SWOT Analysis Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.swot?userdata?.user?.careerDevelopment?.swot:''}
 placeholder="Add SWOT Analysis Link" />
 
 </form>
    </Route>
<Route path={`${path}/journey`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="journey" type="text" title=" Journey Video  Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.journey?userdata?.user?.careerDevelopment?.journey:''}
 placeholder="Add Journey Video  Link" />
 
 </form>
    </Route>
<Route path={`${path}/technical`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="technical" type="text" title=" Technical Knowledge Video  Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.technical?userdata?.user?.careerDevelopment?.technical:''}
 placeholder="Add Technical Knowledge Video  Link" />
 
 </form>
    </Route>
<Route path={`${path}/videoResume`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="videoResume" type="text" title="  Video Resume Link" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.careerDevelopment?.videoResume?userdata?.user?.careerDevelopment?.videoResume:''}
 placeholder="Add  Video Resume  Link" />
 
 </form>
    </Route>
</Switch>

    </>)
}
export default CareerDevelopment;