import React ,{useState}from "react";
import { useSelector } from "react-redux";
import { updateUserData } from "../../../store/actions/User";
import "./Profile.css";
import { useDispatch } from "react-redux";
import { useRouteMatch,Switch, Route } from "react-router-dom";
 
import Input from "../../UI/Input";

import Loading from "../../Loading/Loading";
import EditCard from "../../UI/EditCard";
import Address from "./Address";
const AddressCurrent =({baseUrl}) =>{

  // const {category} = useParams();
  const {url,path} =useRouteMatch();
  const dispatch = useDispatch();

  const userdata = useSelector((state)=>state?.user?.user);
  //console.log(userdata)
  const [data, setData]= useState({field:'',title:'',value:'',id:userdata?.user?.id?userdata?.user?.id:'',token:userdata?.token?userdata?.token:''});
   const [showSnackBar , setSnackBar] = useState({status:false,show:false})
  
 const [loader , setLoader] =useState({status:false});
/* handle Login function  */
const handleUpdate=(e)=>{
    e.preventDefault();
  
setLoader({status:true});
     const user = dispatch(updateUserData(data));

    user.then(response => {
//    console.log(response?.user?.firstName);

if(response?.user){
  //  console.log(response?.user?.lastName);
  setSnackBar({status:true,show:true});
 
  setTimeout(function(){setSnackBar({status:false}); }, 3000);
}
else{
    setSnackBar({status:false,show:true});
 

};
  
setLoader({status:false});



 }).catch(error => {
   //console.error(error);
   setSnackBar({status:false,show:true});
   setLoader({status:false});

 });

    

}
const inputGetValue = 
    (e)=>
    {
             setData({...data,field:e.target.name,title:e.target.title, value:e.target.value});
        
   
    }
    
    return(<>  
    <h3> Current Address </h3>
    
{<Loading Load={loader?.status}/>}
{ (showSnackBar.status && showSnackBar.show ) ?  
<div id="snackbarUser" >{data.value?data.title +"has been Changed to "+data.value : data.title +" has been Removed "}.</div> : ''  }
 { (!showSnackBar.status && showSnackBar.show ) ? 
  <div id="snackbarUser" >Couldn`t update {data.title}  | Error Occured.</div> : '' }

<EditCard  
 url={url} name="currentCity"  icon="" title="Current City" button=""
value=
{userdata?.user?.address?.currentAddress?.currentCity?userdata?.user?.address?.currentAddress?.currentCity:''}
/>

<EditCard  
 url={url} name="currentState"  icon="" title="Current State" button=""
value=
{userdata?.user?.address?.currentAddress?.state?userdata?.user?.address?.currentAddress?.state:''}
/>

<EditCard  
 url={url} name="currentCountry"  icon="" title="Current Country" button=""
value=
{userdata?.user?.address?.currentAddress?.country?userdata?.user?.address?.currentAddress?.country:''}
/>
<EditCard  
 url={url} name="currentZip"  icon="" title="Current Zip/Pin" button=""
value=
{userdata?.user?.address?.currentAddress?.zipOrPin?userdata?.user?.address?.currentAddress?.zipOrPin:''}
/>
<Address baseUrl={baseUrl}/>
<Switch>

<Route path={`${path}/currentCity`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="currentCity" type="text" title=" Current City" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.currentAddress?.currentCity?userdata?.user?.address?.currentAddress?.currentCity:''}
 placeholder="Add Current City" />
 
 </form>
    </Route>
    
<Route path={`${path}/currentState`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="currentState" type="text" title=" Current State" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.currentAddress?.state?userdata?.user?.address?.currentAddress?.state:''}
 placeholder="Add Current State" />
 
 </form>
    </Route>
<Route path={`${path}/currentCountry`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="currentCountry" type="text" title=" Current Country" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.currentAddress?.country?userdata?.user?.address?.currentAddress?.country:''}
 placeholder="Add Current Country" />
 
 </form>
    </Route>
<Route path={`${path}/currentZip`}>
   <form onSubmit={handleUpdate} >
 
 <Input name="currentZip" type="number" title=" Current Zip/Pin" baseUrl={url}  onchange={inputGetValue} value=
 {userdata?.user?.address?.currentAddress?.country?userdata?.user?.address?.currentAddress?.country:''}
 placeholder="Add Current Zip/Pin" />
 
 </form>
    </Route>
</Switch>

    </>)
}
export default AddressCurrent;